.no-result {
	display: flex;
	flex-direction: column;
	height: 100%;
	padding-top: 32px;

	@include respond-to (tablet) {
		padding-top: 4px;
	}

	@include respond-to (mobile) {
		padding-top: 4px;
	}

	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		flex: 1;
	}

	&__txt {
		font-size: 32px;
		margin-bottom: 34px;
		max-width: 340px;
		text-align: center;
		line-height: 1.2;

		@include respond-to (mobile) {
			font-size: 24px;
		}
	}
}