.slider-wrap {
	.swiper-product ~ .swiper-button-wrapper {
		.swiper-button-prev, 
		.swiper-button-next {
			top: clamp(120px, 10vw, 140px);
		}
	}
}

.swiper-product {
	.product-card {
		height: 100%;
		width: 100%;
		max-width: unset;
	
		&__img {
			height: clamp(240px, 21vw, 300px);
			width: unset;
		}
	
		&__title {

			@include respond-to (tablet) {
				font-size: 20px;
			}
	
			@include respond-to (mobile) {
				font-size: 18px;
			}
		}
	
	}
}

