.catalog-list {

	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		column-gap: 120px;
		row-gap: 20px;
		margin-bottom: 26px;

		h2 {
			margin-bottom: unset;
			min-width: max-content;
		}
	}

	&__title {
		flex-grow: 2;
	}

	&__set {
		display: flex;
		align-items: center;
		justify-content: space-between;
		column-gap: 64px;
		font-size: 18px;
		line-height: 27px;
		flex: 1;
		min-width: max-content;

		.value {
			font-weight: 700;
		}

		@include respond-to (tablet) {
			column-gap: 34px;
		}
	}

	&__set-head {
		display: flex;
		align-items: center;
		column-gap: 7px;

		.option {
			display: flex;
			cursor: pointer;
		}

		.title {
			@include respond-to (tablet) {
				font-size: 16px;
			}
		}
	}

	&__sort {
		position: relative;

		.icon {
			width: 24px;
			height: 24px;
			margin-left: 8px;

			img {
				transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);
			}
		}

		.catalog-list__set-head {
			&.js-active {
				.icon img {
					transform: rotate(180deg);
				}
			}
		}

		&-body {
			position: absolute;
			right: 0;
			width: 281px;
			background: #fff;
			box-shadow: 4px 9px 20.399999618530273px 0px #AEAFB0E5;
			padding: 4px 0;
			visibility: hidden;
			opacity: 0;
			top: 50%;
			z-index: 2;
			transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);

			@include respond-to (tablet) {
				right: unset;
				left: 0;
			}

			&.open {
				visibility: visible;
				opacity: 1;
				top: 100%;
			}

			li {
				padding: 10px 14px;
				font-size: 16px;
				cursor: pointer;

				&:hover {
					background: #EFEEEA;
					color: #767676;
				}

				&.active {
					background: #EFEEEA;
					color: #767676;
					display: flex;
					align-items: center;
					justify-content: space-between;

					&::after {
						content: '';
						display: block;
						background: url('../img/icon/check.svg');
						width: 20px;
						height: 20px;
					}
				}
			}
		}
	}

	&__filter {

		.icon {
			width: 16px;
			height: 16px;
		}

		&-head {
			@media(hover:hover) {
				cursor: pointer;
			}
		}

		&-body {

			.accordion-top {
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 24px 0 24px 15px;

				@include respond-to (mobile) {
					padding: 24px 16px;
				}

				@media(hover:hover) {
					&:hover {
						cursor: pointer;
					}
				}
			}
			.accordion-btn {
				width: 24px;
				height: 24px;
			}

			.accordion-item {
				border-bottom: 1px solid #E0DFDF;
			}

			.accordion-content {
				display: none;
				padding: 0 15px 24px;
				margin-top: -16px;
			}

			
		}

		&-price {
			font-size: 16px;

			.row {
				display: flex;
				justify-content: space-between;
				align-items: center;

				span:first-child,
				span:last-child {
					display: flex;
					align-items: center;
					border: 1px solid #DDDEE0;
					justify-content: space-between;
					width: 115px;
					gap: 10px;
					padding: 5px 12px;
				}
				i {
					line-height: 1;
					font-size: 14px;
				}
			}

			input {
				width: 100%;
				font-size: 16px;
				line-height: 27px;
				font-family: 'Nokora', sans-serif;
				border: unset;
			}

			.irs-handle {
				border: unset;
				width: 12px;
				height: 12px;
				background: #000;
				top: 32px;

				&.state_hover {
					background: var(--color-main);
				}
			
				&:hover {
					@media (hover:hover) {
						background: var(--color-main);
					}
				}
			}
			.irs-bar {
				background: #000;
			}
		}
	}

	&__nav {
		padding: 16px 0 0;
	}

	
}