.contacts-page {
	padding-top: 32px;
	padding-bottom: 80px;

	@include respond-to (tablet) {
		padding-bottom: unset;
	}

	@include respond-to (mobile) {
		padding-top: 16px;
	}

	h1 {
		color: #000;
		margin-bottom: 38px;

		@include respond-to (mobile) {
			margin-bottom: 16px;
			font-size: 32px;
		}
	}

	h2 {
		font-weight: 700;
		line-height: 1.1;
		margin-bottom: 16px;

		@include respond-to (laptop) {
			font-size: 28px;
		}

		@include respond-to (mobile) {
			font-size: 24px;
			margin-bottom: 8px;
		}
	}

	a {
		color: var(--color-main);
		line-height: 27px;

		&.mail {
			text-decoration: underline;
		}

		@include respond-to (mobile) {
			font-size: 14px;
			margin-left: -32px;
		}
	}

	.address {
		@include respond-to (mobile) {
			font-size: 14px;
			margin-left: -32px;
		}
	}

	&__wrap {
		display: flex;
		align-items: stretch;
		justify-content: space-between;

		@include respond-to (tablet) {
			flex-direction: column;
		}
	}

	&__row {
		display: flex;
		align-items: flex-start;
		column-gap: 24px;

		&:not(:last-of-type) {
			margin-bottom: 48px;
		}

		@include respond-to (laptop) {
			column-gap: 16px;
		}

		@include respond-to (tablet) {
			margin-bottom: 32px;
		}

		@include respond-to (mobile) {
			column-gap: 8px;
		}
	}

	&__icon {
		width: 40px;
		height: 40px;

		@include respond-to (laptop) {
			width: 35px;
			height: 35px;
		}

		@include respond-to (mobile) {
			width: 24px;
			height: 24px;
		}
	}

	&__map {
		width: 49%;
		height: auto;

		@include respond-to (tablet) {
			width: 100vw;
			height: 343px;
			margin-left: -32px;
			margin-right: -32px;
		}

		@include respond-to (mobile) {
			margin-left: -16px;
			margin-right: -16px;
		}

		iframe {
			height: 100%;
			width: 100%;
		}
	}
}