.btn--burger {
	display: none;
	margin-left: auto;

	@include respond-to(tablet) {
		--line-offset: 2px;
		--color: #000;
		--color-active: var(--color-first);

		position: relative;
		// z-index: 15;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 38px;
		height: 26px;
		cursor: pointer;
		transition: 0.4s cubic-bezier(0.12, 0, 0.39, 0);

		span {
			position: absolute;
			display: block;
			width: 100%;
			height: 2px;
			background: #292D32;
			transition: 0.4s;
			pointer-events: none;

			// &:not(:last-child) {
			// 	margin-bottom: 10px;
			// }

			&:nth-child(1) {
				top: calc(50% - 5px);
				transform: translateY(-50%);
				transition: top 0.2s 0.2s cubic-bezier(0.12, 0, 0.39, 0), transform 0.2s cubic-bezier(0.12, 0, 0.39, 0);
			}

			&:nth-child(2) {
				top: calc(50% + 5px);
				transform: translateY(-50%);
				transition: top 0.2s 0.2s cubic-bezier(0.12, 0, 0.39, 0), transform 0.2s cubic-bezier(0.12, 0, 0.39, 0);
			}

			// &:nth-child(3) {
			// 	top: 100%;
			// 	transform: translateY(-100%);
			// }
		}

		&.js-active {
			width: 24px;
			height: 24px;

			span {
				// top: 50%;
				// transform: translateY(-50%);

				&:nth-child(1) {
					top: 50%;
					transform: translateY(-50%) rotate(45deg);
					transition: top 0.2s cubic-bezier(0.12, 0, 0.39, 0), transform 0.2s 0.2s cubic-bezier(0.12, 0, 0.39, 0);;
				}

				&:nth-child(2) {
					top: 50%;
					transform: translateY(-50%) rotate(-45deg);
					transition: top 0.2s cubic-bezier(0.12, 0, 0.39, 0), transform 0.2s 0.2s cubic-bezier(0.12, 0, 0.39, 0);;
				}

			}
		}
	}
}
