.shop-sale {
	.sale {
		&__wrap {
			&:not(:last-of-type) {
				margin-bottom: 42px;

				@media screen and (max-width: 1024px) {
					margin-bottom: 16px;
					flex-wrap: wrap-reverse;
				}
			}

			&:nth-child(2n) {
				flex-direction: row-reverse;
			}

			@media screen and (max-width: 1024px) {
				gap: 16px;
			}

		}

		&__box {
			@include respond-to (tablet) {
				min-width: calc(50% - 8px);
				height: 289px;
			}

			@include respond-to (mobile) {
				flex: 1;
				height: 144px;
			}

			.sale__desc {
				@include respond-to (mobile) {
					bottom: 10px;
				}
			}

			&--bg {

				@media screen and (max-width: 1024px) {
					min-width: calc(50% - 8px);
				}

				@include respond-to (tablet) {
					height: 389px;
				}

				@include respond-to (mobile) {
					flex: unset;
					height: 300px;
				}

				.sale__desc {
					@include respond-to (mobile) {
						bottom: 32px;
					}
				}

				
			}

		}
	}
}