.banner {
	position: relative;
	min-height: 355px;
	overflow: hidden;
	display: flex;
	align-items: center;

	@include respond-to (tablet) {
		min-height: 400px;
	}

	@include respond-to (mobile) {
		min-height: 300px;
		align-items: flex-start;
	}

	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: -150px;
		width: calc(57% + 150px);
		min-width: 850px;
		height: 100%;
		background: #000;
		opacity: 0.4;
		z-index: 2;
		transform: skew(-37deg);

		@include respond-to (laptop) {
			min-width: 730px;
		}

		@include respond-to (tablet) {
			min-width: clamp(490px, 113vw, 670px);
			transform: skew(-21deg);
		}

		@include respond-to (mobile) {
			min-width: clamp(477px, 132vw, 670px);
		}
	}

	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 1;

		img {
			height: 100%;
			object-fit: cover;
			object-position: center center;
		}
	}

	&__content {
		position: relative;
		z-index: 3;
		max-width: 602px;

		p {
			color: #fff;
			margin-bottom: 16px;
		}

		@include respond-to (laptop) {
			max-width: 480px;
		}

	}

	&--page {
		.banner__img {
			background: #000;

			img {
				opacity: 0.8;
			}
		}

		.banner__content {
			max-width: 447px;

			@include respond-to (mobile) {
				max-width: 310px;
			}
		}

		&::after {

			@include respond-to (tablet) {
				transform: skew(-33deg);
				min-width: clamp(477px, 140vw, 670px) !important;
			}
		}
	}

	&--sm {
		padding: 47px;

		@include respond-to (tablet) {
			padding: 30px;
		}

		@include respond-to (mobile) {
			padding: 16px 12px;
			min-height: 400px;
		}


		.banner__img {
			img {
				height: 112%;
				object-position: 0 75%;
			}
		}

		h1, 
		.banner__content {
			@include respond-to (mobile) {
				text-align: center;
			}
		}
	}
}