// $name - имя шрифтового семейства;
// $file - имя файла;
// $weight - жирность шрифта (по-умолчанию установлено значение 400, но если мы передадим параметр, то значение по-умолчанию будет проигнорировано)
// $style - стиль начертания (тоже установлен по-умолчанию normal)

@mixin font-face($name, $file, $weight: 400, $style: normal) {
	@font-face {
		font-family: "#{$name}";
		src: local("#{$file}"), url("../fonts/#{$file}.woff2") format("woff2"),
			url("../fonts/#{$file}.woff") format("woff");
		font-weight: $weight;
		font-style: $style;
		font-display: swap;
	}
}

@include font-face("Nokora", "Nokora-Regular", 400);
@include font-face("Nokora", "Nokora-Bold", 700);
@include font-face("Inter", "Inter-Regular", 400);


button {
	border: unset;
	background: unset;
	padding: unset;
	font-family: "Nokora";
	cursor: pointer;
}

a.more {
	color: var(--color-main);
	min-width: max-content;
	text-decoration: underline;
	transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);

	@media(hover:hover) {
		&:hover {
			color: var(--color-hover);
		}
	}

	@include respond-to (mobile) {
		font-size: 14px;
	}
}

.btn,
a.btn {
	display: flex;
	font-weight: 700;
	font-size: 20px;
	// line-height: 1.5;
	color: hsl(0, 0%, 100%);
	background: var(--color-button);
	padding: 15px 30px;
	width: 100%;
	max-width: 343px;
	align-items: center;
	justify-content: center;
	transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);

	&--sm {
		padding: 10px 25px;
		max-width: 309px;
		margin: 0 auto;
		font-size: 18px;
		line-height: 150%;
	}

	@media(hover:hover) {
		&:hover {
			background: var(--color-hover);
		}
	}

	@include respond-to (tablet) {
		font-size: 18px;
	}

	@include respond-to (mobile) {
		width: 100%;
		max-width: unset;
		margin: 0 auto;
		padding: 10px 20px;
	}

	&--border {
		color: var(--color-text-dark);
		background: unset;
		border: 1px solid var(--color-text-dark);
		padding: 14px 24px;
		font-weight: 400;
		font-size: 18px;

		@media(hover:hover) {
			&:hover {
				background: unset;
				color: var(--color-main);
				border: 1px solid var(--color-main);
			}
		}
	}

	&--wish {
		width: auto;
		padding: 15px;
		border: 1px solid #BDBDBD;

		svg {
			width: 24px;
			height: 24px;

			@include respond-to (mobile) {
				width: 20px;
				height: 20px;
			}

			path {
				stroke: #BDBDBD;
				transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);
			}
		}

		@media(hover:hover) {
			&:hover {
				cursor: pointer;

				svg path {
					stroke: var(--color-main);
				}
			}
		}

		@include respond-to (mobile) {
			width: auto;
			padding: 13px;
		}

		&.active {
			border: 1px solid var(--color-main);
			background: var(--color-main);

			svg path {
				stroke: #fff;
			}

			@media(hover:hover) {
				&:hover {
					cursor: pointer;
					border: 1px solid var(--color-main);
					background: var(--color-main);
	
					svg path {
						stroke: #fff;
					}
				}
			}
		}
	}

	&.cart {
		&.active {
			background: transparent;
			color: var(--color-main);
			border: 1px solid var(--color-main);
		}
	}
}

.slider-wrap {
	position: relative;
}

.swiper-pagination {
	position: relative;
	bottom: unset;
	padding-top: 26px;
	display: flex;
	align-items: center;
	justify-content: center;

	@include respond-to (tablet) {
		padding-top: 20px;
	}
}

.swiper-pagination-bullet {
	opacity: unset;
	width: 30px;
	height: 30px;
	background: unset;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: var(--anim4);
	margin: unset !important;

	&:not(:last-child) {
		margin: 0 14px 0 0 !important;
	}

	&::after {
		content: '';
		display: block;
		border-radius: 100%;
		width: 3px;
		height: 3px;
		background: #8A8A8A;
	}

	&::before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		border-radius: 100%;
		width: 0;
		height: 0;
		border: 1px solid transparent;
		transition: 0.4s cubic-bezier(0.12, 0, 0.39, 0);
	}

	@media(hover:hover) {
		&:hover {
			cursor: pointer;

			&::after {
				background: var(--color-main);
			}
		}
	}
}

.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal {
	bottom: unset;
}

.swiper-pagination-bullet-active {
	// border: 1px solid rgb(153, 127, 90, 0.5);

	&::after {
		background: var(--color-main);
	}

	&::before {
		border: 1px solid rgb(153, 127, 90, 0.5);
		width: 100%;
		height: 100%;
	}
}

.swiper-button-prev, 
.swiper-button-next {
	top: calc(50% - 30px);
	transform: translateY(-50%);
	width: 72px;
	height: 72px;
	background: #fff;
	border-radius: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: unset;
	box-shadow: 0 9px 30px 0 rgba(206, 207, 209, 0.9);

	svg {
		width: 24px;
	}

	&::after {
		display: none;
	}

	@media(hover:hover) {
		&:hover {
			svg {
				path {
					fill: var(--color-main);
				}
			}
		}
	}

	@include respond-to (laptop) {
		width: 52px;
		height: 52px;
	}

	@include respond-to (tablet) {
		display: none;
	}
}

.swiper-button-next {
	transform: translateY(-50%) scaleX(-1);
	right: -36px;

	@include respond-to (laptop) {
		right: -26px;
	}
}

.swiper-button-prev {
	left: -32px;

	@include respond-to (laptop) {
		left: -26px;
	}
}

.swiper-button-prev.swiper-button-disabled, 
.swiper-button-next.swiper-button-disabled {
	display: none;
}

.swiper-category {

	.swiper-pagination,
	.swiper-button-wrapper {
		display: none;
	}

	.swiper-wrapper {
		align-items: stretch;
	}

	.swiper-slide {
		height: auto;
		overflow: hidden;
	}

	

	&__box {
		height: 100%;
		display: block;
		position: relative;

		@media(hover:hover) {
			&:hover {
				.swiper-category__img {
					transform: scale(1.2);
				}
				.swiper-category__title {
					font-size: clamp(20px, 2vw, 28px);
				}
			}
		}
	}

	&__img {
		height: 100%;
		position: relative;
		transition: 0.4s cubic-bezier(0.12, 0, 0.39, 0);
		font-size: 0;
		display: flex;
		align-items: center;
		justify-content: center;

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: hsl(0, 0%, 0%);
			opacity: 0.3;
		}

		img {
			height: 100%;
			min-width: 240px;
			height: clamp(300px, 26vw, 370px);
			object-fit: cover;
		}
	}

	&__title {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		font-weight: 700;
		font-size: clamp(20px, 1.8vw, 24px);
		line-height: 120%;
		color: #fff;
		text-transform: uppercase;
		width: 100%;
		text-align: center;
		transition: var(--anim4);
	}
}

.wish {
	width: 24px;
	height: 24px;
	cursor: pointer;

	svg path {
		transition: var(--anim4);
	}

	&.check {
		svg path {
			fill: var(--color-main);
			stroke: var(--color-main);
		}
	}

	@media(hover:hover) {
		&:hover{
			svg path {
				stroke: var(--color-main);
			}
		}
	}

}

.right-box {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	background: #fff;
	z-index: 16;
	width: 100%;
	max-width: 480px;
	visibility: hidden;
	opacity: 0;
	right: -50%;
	transition: 0.4s cubic-bezier(0.12, 0, 0.39, 0);
	display: flex;
	flex-direction: column;

	@include respond-to (tablet) {
		max-width: unset;
	}

	&.open {
		visibility: visible;
		opacity: 1;
		right: 0;
	}

	&__top {
		border-bottom: 1px solid #E0DFDF;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 24px 32px 0;

		@include respond-to (mobile) {
			padding: 24px 16px;
		}
	}

	&__title {
		padding: 24px 15px;
		font-weight: 700;
		font-size: 20px;

		@include respond-to (mobile) {
			padding: unset;
		}
	}

	&__close {
		width: 12px;
		height: 12px;
		font-size: 0;

		svg path {
			transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);
		}

		@media(hover:hover) {
			&:hover {
				cursor: pointer;

				svg path {
					stroke: var(--color-main);
				}
			}
		}
	}

	&__body {
		height: 100vh;
		overflow-y: auto;
		&.scroll-wrapper {
			padding: 16px 32px !important;
			
			@include respond-to (mobile) {
				padding: 16px !important;
			}
		}
		.accordion-wrap {
			padding: 16px 32px;
			@include respond-to(mobile) {
				padding: 16px 0;
			}
		}
	}

	&__button {
		width: calc(100% - 64px);
		margin: auto auto 78px;
		padding-top: 20px;

		@include respond-to (mobile) {
			width: calc(100% - 32px);
			margin: auto auto 64px;
			padding-top: 20px;
		}

		button {
			max-width: unset;

			@media(hover:hover) {

				&:hover{
					cursor: pointer;
				}
			}
		}

		.order__more {
			display: flex;
			justify-content: space-between;
			margin-bottom: 17px;
			.cash {
				font-weight: 700;
			}
		}
	}

	.cart {
		&__product {		
			&-row {
				display: flex;
				gap: 8px;
				width: 100%;
				&:not(:last-child) {
					border-bottom: 1px solid #E0DFDF;
					padding-bottom: 24px;
					margin-bottom: 24px;
					@include respond-to(mobile) {
						padding-bottom: 16px;
						margin-bottom: 16px;
					}
				}
			}
			&-img {
				img {
					width: 120px;
					height: 120px;
					object-fit: cover;
					object-position: center;
				}
			}
			&-top {
				display: flex;
				font-size: 18px;
				font-weight: 700;
				justify-content: space-between;
				gap: 10px;
				padding-bottom: 8px;

				@include respond-to(mobile) {
					font-size: 14px;
				}

				.delete {
					flex-shrink: 0;
					cursor: pointer;
				}
			}
			&__info {
				flex: 1;
				font-size: 14px;
				line-height: 27px;

				@include respond-to(mobile) {
					font-size: 12px;
				}
			}
			&-color {
				display: flex;
				align-items: center;
				gap: 8px;
				margin-bottom: 2px;

				.title {
					@include respond-to(mobile) {
						padding-top: 4px;
					}
				}

				.color {
					font-size: 0;
					line-height: 1;
				}
				img {
					width: 24px;
					height: 24px;
					border: 1px solid #EFEEEA;

					@include respond-to(mobile) {
						width: 16px;
						height: 16px;
					}
				}
			}
			&-conf {
				display: flex;
				gap: 8px;
				margin-bottom: 2px;
			}
			&-count {
				display: flex;
				gap: 8px;
			}
			&-bottom {
				display: flex;
				justify-content: space-between;
				gap: 8px;
			}
		}
	}
}

.product-list {
	display: flex;
	align-items: stretch;
	column-gap: 37px;
	row-gap: 42px;
	flex-wrap: wrap;
	// justify-content: space-between;
	margin-bottom: 32px;

	@include respond-to (laptop) {
		column-gap: 20px;
	}

	@include respond-to (tablet) {
		row-gap: 20px;
	}

	@include respond-to (mobile) {
		margin-bottom: 16px;
		row-gap: 16px;
	}

	.product-card {
		width: calc((100% / 4) - 28px);

		@include respond-to (laptop) {
			width: calc((100% - 40px) / 3);
		}

		@include respond-to (tablet) {
			width: calc((100% - 20px) / 2);
			max-width: unset;
		}

		@include respond-to (mobile) {
			width: 100%;
		}

		&__img {
			@include respond-to (laptop) {
				height: clamp(200px, 25vw, 300px);
			}

			@include respond-to (tablet) {
				height: clamp(200px, 40vw, 300px);
			}

			@include respond-to (mobile) {
				height: 300px;
			}
		}
	}
}

.product-card {
	display: flex;
	flex-direction: column;
	width: clamp(260px, 21vw, 300px);
	max-width: 300px;

	&__img {
		position: relative;
		height: clamp(240px, 21vw, 300px);
		width: 100%;
		margin-bottom: 27px;

		img {
			height: 100%;
			object-fit: cover;
		}

		.hover {
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0;
			opacity: 0;
			transition: 0.4s cubic-bezier(0.12, 0, 0.39, 0);
		}
	}

	&__title {
		font-size: 20px;
		line-height: 27px;
		transition: var(--anim4);
		margin-bottom: 8px;

		@include respond-to (tablet) {
			font-size: 18px;
		}
	}

	&__desc {
		display: flex;
		flex-direction: column;
		flex: 1;
	}

	&__more {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: auto;
	}

	&__price {
		font-size: 20px;
		line-height: 27px;
		font-weight: 700;

		&.sale-price {
			color: var(--color-accent);

			span {
				color: #8A8A8A;
				position: relative;
				margin-left: 8px;

				&::after {
					content: '';
					position: absolute;
					top: calc(50% - 1px);
					left: - 2px;
					width: calc(100% + 4px);
					height: 1px;
					background: #000;
				}
			}
		}
	}

	@media(hover:hover) {

		.wish {
			opacity: 0;
			visibility: hidden;
			transition: var(--anim4);
		}

		&:hover {

			.product-card__title {
				color: var(--color-main);
			}

			.product-card__img {
				.hover {
					opacity: 1;
				}
			}

			.wish {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

.breadcrumbs {
	display: flex;
	align-items: center;
	font-size: 16px;
	color: var(--color-text-light);
	padding: 16px 0;
	min-width: max-content;

	@include respond-to (tablet) {
		font-size: 14px;
		padding: 8px 0;
	}

	li {
		&:not(:last-child) {
			&::after {
				content: '/';
				padding: 0 12px;

				@include respond-to (mobile) {
					padding: 0 4px;
				}
			}
		}
	}
}

.blog__categories {
	display: flex;
	gap: 32px;
	border-top: 1px solid #8a8a8a;

	li {
		padding-top: 20px;
		margin-bottom: 24px;
		border-top: 5px solid transparent;
		&.active {
			border-top-color: #1E1E1E;
			color: var(--color-main);
		}
		@media (hover:hover) {
			&:hover{
				color: var(--color-hover);
			}
		}
	}
}

.pagination {
	padding: 16px 0;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 10px;

	li {
		a {
			width: 34px;
			height: 34px;
			display: flex;
			align-items: center;
			justify-content: center;
			box-shadow: 0px 3px 2px 0px rgba(224, 224, 224, 0.3);
			border-radius: 4px;

			img {
				width: 24px;
				height: 24px;
			}
		}

		&.active {
			a {
				background: var(--color-main);
				color: #fff;
			}
		}

		&.next {
			a {
				img {
					transform: scale(-1);
				}
			}
		}
	}
}

.input-row {
	display: flex;
	align-items: center;

	&:not(:last-of-type) {
		margin-bottom: 10px;
	}

	input[type="checkbox"] {
		width: 18px;
		height: 18px;
		border: 1px solid #DDDEE0;
		margin: unset;
		margin-right: 8px;

		@media(hover:hover) {
			cursor: pointer;
		}

		&:checked {
			border: 1px solid #000;
			background: #000;
			background-image: url('../img/icon/check-input.svg');
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 13px 13px;
		}

	}

	label {
		line-height: 1;
		font-size: 16px;
		color: #222222;

		@media(hover:hover) {
			cursor: pointer;
		}
	}

	@media(hover:hover) {
		cursor: pointer;
	}
}

.back {
	display: none;

	&::before {
		content: '';
		display: block;
		width: 16px;
		height: 16px;
		background: url('../img/icon/arrow.svg') no-repeat;
		background-size: contain;
	}

	@include respond-to (tablet) {
		display: flex;
		font-size: 14px;
		margin-bottom: 24px;
		align-items: center;
		gap: 8px;
	}
}

.mfp-container {
	@include respond-to (mobile) {
		padding-left: unset;
		padding-right: unset;
	}
}

.mfp-content {
	max-width: max-content;
	background: #fff;

	input,
	textarea {
		font-family: 'Nokora';
		width: 100%;
		border: 1px solid #D0D5DD;
		box-shadow: 0px 1px 2px 0px #1018280D;
		padding: 14px;
		font-size: 16px;
		background: #fff;

		&::placeholder {
			color: var(--color-form);
			
		}

		&[type="checkbox"] {
			width: 24px;
			height: 24px;
			padding: unset;

			& ~ label {
				font-size: 14px;
				color: #222222;
			}
		}
	}

	textarea {
		resize: vertical;
		max-height: 300px;
		min-height: 48px;
	}

	button {
		&.btn {
			width: 100%;
			max-width: unset;
		}
	}

	.mfp-close {
		width: 24px;
		height: 24px;
		font-size: 0;
		background-image: url('../img/icon/close.svg');
		background-size: 12px 12px;
		background-repeat: no-repeat;
		background-position: center;
		opacity: 1;
		top: 16px;
		right: 16px;

		@media(hover:hover) {
			&:hover {
				color: var(--color-main);
			}
		}

		@include respond-to (tablet) {
			top: 24px; 
		}
	}
}
.form {
	display: flex;
	align-items: center;
	background: #F8F8F8;
	box-shadow: -13px 20px 30px 0px #00000040;

	&__wrap {
		padding: 48px;
		width: clamp(375px, 60vw, 536px);
		flex-shrink: 1;

		@include respond-to (tablet) {
			padding: 55px 16px 24px;
		}
	}

	&__input,
	&__input.input-row {
		margin-bottom: 24px;
	}

	&__img {
		font-size: 0;
		max-width: 500px;
		width: 50%;
		height: 100%;
		overflow: hidden;

		img {
			width: unset;
			height: 100%;
			object-fit: cover;
		}

		@include respond-to (tablet) {
			display: none;
		}
	}

	&-reviews {
		.stars {
			display: flex;
			align-items: center;
			gap: 10px;
			
		}

		.star {
			width: 24px;
			height: 24px;
			cursor: pointer;

			svg path {
				fill: #BCBCBC;
			}
		}
	}


}

.thank {
	padding: 48px;
	text-align: center;

	@include respond-to (mobile) {
		padding: 24px;
	}

	&__img {
		margin-bottom: 17px;

		img {
			width: 140px;

			@include respond-to (mobile) {
				width: 60px;
			}
		}
		
	}

	&__txt {
		p {
			margin-bottom: unset;
			font-size: 20px;

			@include respond-to (mobile) {
				font-size: 18px;
			}
		}
	}

}

.no-close {
	.mfp-close {
		display: none;
	}
}

.rating-input {
	align-items: center;
	display: flex;
	flex-direction: row-reverse;
	input {
		display: none;
	}
	input:checked + label::before {
		content: none;
	}
	label {
		display: block;
		width: 30px;
		height: 30px;
		cursor: pointer;
		background: center / contain no-repeat url("../img/icon/star-gray.svg");
	}

	input:hover ~ label::after,
	input:checked ~ label::after {
		content: '';
		width: 30px;
		height: 30px;
		position: absolute;
		background: center / contain no-repeat url("../img/icon/star.svg");
	}
}