.page-404 {
	.banner {
		height: calc(100vh - 143px -  282px);
		min-height: unset;

		@include respond-to (tablet) {
			height: calc(100vh - 141px -  318px);
		}

		@include respond-to (mobile) {
			height: calc(100vh - 131px -  318px);
		}

		&__content {
			max-width: 403px;

			@include respond-to (mobile) {
				max-width: unset;
				width: 100%;
			}

			p {
				font-size: 32px;
				line-height: 43px;
				font-weight: 700;

				@include respond-to (tablet) {
					font-size: 24px;
					line-height: 27px;
				}
			}
		}
	}
}