.footer {
	background: hsl(0, 0%, 97%);
	padding: 48px 0 32px;

	@include respond-to (tablet) {
		padding: 24px 0 14px;
	}

	&__wrapper {
		display: grid;
		grid-template-columns: 1fr auto;
		grid-template-areas: 
			"logo nav"
			"bottom bottom";

		@include respond-to(tablet) {
			grid-template-areas: 
				"logo"
				"nav"
				"bottom";
		}

		@include respond-to(mobile) {

		}
	}

	&__logo {
		grid-area: logo;

		img {
			width: 191px;
		}

		@include respond-to (tablet) {
			margin: 0 auto 24px;
		}
	}

	&__nav {
		grid-area: nav;
		margin-left: auto;
		display: flex;
		column-gap: 112px;
		margin-bottom: 24px;

		a {
			display: block;
			padding-bottom: 16px;
			transition: var(--anim2);
			max-width: max-content;
			font-size: 14px;
	
			@media(hover:hover) {
				&:hover {
					color: var(--color-hover)
				}
			}
		}

		@include respond-to (laptop) {
			column-gap: 60px;
		}

		@include respond-to (tablet) {
			column-gap: 20px;
			margin-left: unset;
			justify-content: space-between;
		}
	}

	&__bottom {
		grid-area: bottom;
		display: flex;
		justify-content: space-between;
		border-top: 1px solid hsl(0, 0%, 54%);
		padding-top: 32px;

		.copy {
			p {
				font-size: 14px;
				line-height: 193%;
				margin-bottom: unset;
			}
		}

		.ocean {
			width: 84px;
		}
	}



}
