.product-page {
	overflow-x: hidden;
}

.product-main {
	display: flex;
	align-items: stretch;
	gap: 48px;
	padding-top: unset;
	padding-bottom: 48px;

	@include respond-to (laptop) {
		flex-wrap: wrap;
		gap: 24px;
	}

	@include respond-to (mobile) {
		padding-bottom: unset;
	}

	&__img {
		width: calc(50% - 24px);
		min-width: 480px;

		@include respond-to (laptop) {
			width: 100%;
			min-width: unset;
		}

		@include respond-to (tablet) {
			margin-left: -32px;
			margin-right: -32px;
			width: calc(100% + 64px);
		}

		@include respond-to (mobile) {
			margin-left: -16px;
			margin-right: -16px;
			width: calc(100% + 32px);
		}
	}

	&__info {
		@include respond-to (mobile) {
			width: 100%;
		}
	}

	&__title {
		font-weight: 700;
		font-size: 32px;
		line-height: 1;
		margin-bottom: 16px;

		@include respond-to (mobile) {
			font-size: 20px;
			margin-bottom: 8px;
		}
	}

	&__price {
		font-size: 32px;
		line-height: 1;
		margin-bottom: 24px;

		@include respond-to (mobile) {
			font-size: 20px;
			margin-bottom: 16px;
		}

		&.sale {
			color: var(--color-accent);

			span {
				color: var(--color-text);
				font-size: 18px;
				line-height: 1;
				text-decoration: line-through;
				margin-left: 16px;

				@include respond-to (mobile) {
					font-size: 14px;
					margin-left: 8px;
				}
			}
		}
	}

	&__color,
	&__config {
		display: flex;
		align-items: start;
		margin-bottom: 24px;
		gap: 2px;

		@include respond-to (mobile) {
			margin-bottom: 16px;
			row-gap: 4px;
		}

		.form_input_wrap {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
		}

		input {
			display: none;
		}

		label {
			width: 44px;
			height: 44px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 0;
			border: 2px solid transparent;
			transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);
			cursor: pointer;

			@include respond-to (mobile) {
				width: 36px;
				height: 36px;
			}

			img {
				width: 32px;
				height: 32px;
				border: 1px solid #EFEEEA;
				transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);

				@include respond-to (mobile) {
					width: 24px;
					height: 24px;
				}
			}
		}

		input:checked ~ label {
			border: 2px solid var(--color-main);

			img {
				border: unset;
				border: 1px solid transparent;
			}
		}
	}

	&__count {
		margin-bottom: 42px;
		display: flex;
		align-items: center;

		@include respond-to (mobile) {
			margin-bottom: 24px;
		}
		
		.form_input_wrap  {
			display: flex;
			font-size: 0;
		}

		button {
			cursor: pointer;
		}

		button,
		input.quantity {
			font-size: 18px;
			line-height: 1;
			font-family: "Nokora";
			width: 45px;
			height: 45px;
			text-align: center;
			border: 1px solid #616161;

			@include respond-to (tablet) {
				width: 36px;
				height: 36px;
				font-size: 16px;
			}
		}

		input.quantity {
			border: unset;
			border-top: 1px solid #616161;
			border-bottom: 1px solid #616161;
		}

		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			-webkit-appearance: none;
}
	}

	&__button {
		display: flex;
		gap: 24px;
		align-items: stretch;

		@include respond-to (mobile) {
			gap: 16px;
			width: 100%;
		}
	}

	.title {
		margin-right: 16px;
		padding: 13px 0;

		@include respond-to (tablet) {
			font-size: 16px;
		}

		@include respond-to (mobile) {
			
			padding: 10px 0;
		}
	}

	a.more {
		margin-left: 2px;

		@include respond-to (mobile) {
			margin-left: 8px;
		}
	}

	.config {

		&__info {
			display: none;
			width: 262px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 15;
			background: #fff;

			&.active {
				display: block;
			}
		}

		&__desc {
			padding: 8px 13px 16px;
		}

		&__title {
			margin-bottom: 10px;
		}

		&__prop {
			font-size: 14px;
			margin-bottom: 22px;
		}

		&__txt {
			font-size: 14px;
			line-height: 22px;
		}

		&__close {
			position: absolute;
			top: 15px;
			right: 15px;

			svg {
				width: 12px;
				height: 12px;
			}
		}
	}

}