.header {
	position: relative;
	max-height: 156px;
	
	@include respond-to(tablet) {
		z-index: 15;
	}

	&--order {
		background: #F3F3F3;
		border-bottom: 1px solid #DDDEE0;

		.header__wrapper {
			display: none;
		}

		.header__wrapper--order {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 8px 0;
		}

		.header__tel {
			min-width: max-content;
			width: 20%;

			a {
				display: flex;
				align-items: center;
				justify-content: flex-end;
				gap: 4px;

				@include respond-to (tablet) {
					font-size: 16px;
				}

				span.icon {
					width: 24px;
					height: 24px;
					margin-bottom: 2px;
				}

				span {
					color: #312F2F;
				}
			}
			
		}
		.header__back {
			width: 20%;

			@include respond-to (tablet) {
				position: absolute;
				left: 32px;
				top: calc(100% + 18px);
			}

			@include respond-to (mobile) {
				left: 16px;
			}

			.back {
				display: flex;

				@include respond-to (tablet) {
					font-size: 16px;
				}
			}
		}

		.header__logo {
			width: 60%;

			@include respond-to (tablet) {
				width: auto;
				margin: unset;
				border-bottom: unset;
				padding: 8px 0;
			}
		}
	}

	&__wrapper--order {
		display: none;
	}

	&__wrapper {
		display: grid;
		align-items: center;
		grid-template-columns: 112px 1fr 112px;
		// row-gap: 8px;
		grid-template-areas: 
		"search logo icons"
		"nav nav nav";

		@include respond-to (tablet) {
			grid-template-columns: auto auto;
			grid-template-areas: 
				"logo logo"
				"search nav";
		}
	}

	&__search {
		grid-area: search;
		padding-left: 32px;

		@include respond-to (tablet) {
			padding: 16px 0;
		}

		&-icon {
			width: 24px;
			cursor: pointer;
			font-size: 0;

			svg path {
				transition: var(--anim2);
			}

			&:hover {
				svg path {
					stroke: var(--color-main);
				}
			}
		}

		&-input {
			display: none;
			position: absolute;
			width: 100%;
			height: 100vh;
			top: 0;
			left: 0;
			background: #fff;

			@include respond-to (tablet) {
				top: 83px;
			}

			@include respond-to (mobile) {
				top: 73px;
			}
		}

		&-head {
			display: flex;
			align-items: center;
			justify-content: space-between;
			max-width: 1440px;
			margin: 0 auto;
			padding: 10px 64px;

			@include respond-to (tablet) {
				padding: unset;
			}
		}

		&-body  {
			position: relative;
			margin: 0 auto;
			margin-top: 10%;
			border-bottom: 1px solid #000;
			padding: 8px 16px;
			width: 45%;
			max-width: 600px;
			min-width: 340px;
			display: flex;
			align-items: center;
			z-index: 20;

			@include respond-to (tablet) {
				margin-top: 16px;
				width: calc(100% - 64px);
				max-width: unset;
				min-width: unset;
			}

			@include respond-to (mobile) {
				width: calc(100% - 32px);
				padding: 8px;
				margin-top: 8px;
			}

			input {
				border: unset;
				outline: unset;
				padding: 0 16px 1px;
				flex: 1;
				font-size: 18px;
				line-height: 27px;

				@include respond-to (mobile) {
					font-size: 14px;
				}

				&::placeholder {
					font-weight: 700;
					color: #ADADAD;
				}
			}

			input[type="search"]::-webkit-search-decoration,
			input[type="search"]::-webkit-search-cancel-button,
			input[type="search"]::-webkit-search-results-button,
			input[type="search"]::-webkit-search-results-decoration {
				-webkit-appearance:none;
			}

			label {
				font-weight: 700;
				color: var(--color-text-light);

				@include respond-to (mobile) {
					font-size: 14px;
				}
			}

			button {
				svg {
					width: 24px;
					height: 25px;
				}

				@include respond-to (tablet) {
					display: none;
				}

				@media(hover:hover) {
					cursor: pointer;

					&:hover {
						svg path {
							stroke: var(--color-main);
						}
					}
				}
			}
		}

		&-logo {
			max-width: 257px;

			@include respond-to (tablet) {
				display: none;
			}
		}

		&-close {
			width: 18px;
			height: 18px;
			cursor: pointer;

			svg path {
				transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);
			}

			@include respond-to (tablet) {
				position: absolute;
				top: 29px;
				right: 48px;
				z-index: 21;
			}

			@include respond-to (mobile) {
				width: 12px;
				height: 12px;
				top: 21px;
				right: 24px;
			}

			@media(hover:hover) {
				&:hover {
					svg path {
						stroke: var(--color-main);
					}
				}
			}
		}
	}

	&__logo {
		grid-area: logo;
		margin: 0 auto;
		width: 100%;
		padding: 8px 0;

		@include respond-to (tablet) {
			border-bottom: 1px solid hsl(0deg, 0%, 54%);
			padding: 16px 0;
		}

		a {
			display: flex;
			justify-content: center;
		}

		img {
			width: 100%;
			max-width: 257px;
			max-height: 69px;
			object-fit: contain;

			@include respond-to (tablet) {
				max-width: 230px;
				height: 50px;
			}

			@include respond-to (mobile) {
				max-width: 216px;
				height: 40px;
			}
		}
	}

	&__icons {
		grid-area: icons;
		justify-content: flex-end;
		gap: 48px;
		display: flex;
		padding-right: 32px;

		svg path {
			transition: var(--anim2);
		}

		a:hover,
		.header__cart:hover {
			cursor: pointer;
			
			svg path {
				stroke: var(--color-main);
			}
		}

		@include respond-to (tablet) {
			display: none;
		}
	}

	&__wish {
		
		svg {
			width: 32px;
		}
	}

	&__cart {
		svg {
			width: 32px;
		}
	}

	&__nav {
		grid-area: nav;
		border-top: 1px solid hsl(0, 0%, 54%);
		padding-top: 8px;
		position: relative;

		@include respond-to (tablet) {
			border-top: unset;
			padding: 16px 0;
			position: unset;
		}

		.menu {
			display: flex;
			gap: 48px;
			justify-content: center;

			@include respond-to (tablet) {
				position: absolute;
				display: block;
				left: 0;
				right: 0;
				top: 50%;
				opacity: 0;
				visibility: hidden;
				background: #fff;
				padding: 0 32px 332px;
				transition: 0.3s cubic-bezier(0.12, 0, 0.39, 0);
	
				&.js-open {
					top: 100%;
					opacity: 1;
					visibility: visible;

					.dropdown {
						margin-top: 0;
						transition: none;

						&__left {
							visibility: visible;
							transition: none;
						}
					}
				}
			}

			@include respond-to (mobile) {
				padding: 0 16px 332px;
			}

			& > li {
				padding: 16px 0;

				&.mob {
					display: none;

					@include respond-to (tablet) {
						display: block;
					}
				}

				& > a {
					transition: var(--anim2);

					@include respond-to (tablet) {
						font-size: 24px;
					}
				}
			}

			@media(hover:hover) {
				li {
					&:hover > a {
						color: var(--color-main);
					}
	
					&:hover > .dropdown {
						opacity: 1;
						visibility: visible;
					}
				}
			}

			

			& > a {
				font-size: 18px;
				line-height: 150%;
				color: var(--color-text-dark);
				transition: var(--anim2);

				@media(hover:hover) {
					&:hover {
						color: var(--color-main);
					}
				}
			}
		}

		.dropdown {
			position: absolute;
			background: hsl(0, 0%, 100%);
			padding: 24px 64px 32px 64px;
			margin-left: -64px;
			margin-right: -64px;
			top: 100%;
			left: 0;
			right: 0;
			z-index: 5;
			display: flex;
			align-items: stretch;
			transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);
			opacity: 0;
			visibility: hidden;
			// opacity: 1;
			// visibility: visible;

			@include respond-to (tablet) {
				margin-left: unset;
				margin-right: unset;
				padding: 16px 32px 0;
				opacity: 1;
				position: unset;
				align-items: flex-start;
				visibility: visible;
				margin-top: -500%;
				transition: all 0.3s cubic-bezier(0.12, 0, 0.39, 0) 0.4s;
			}

			@include respond-to (mobile) {
				padding: 16px 16px;
			}

			&__left {
				width: 30vw;
				max-width: 435px;
				min-width: 330px;
				position: relative;
				overflow: hidden;
				margin-right: clamp(20px, 7.8vw, 112px);

				@include respond-to (tablet) {
					position: absolute;
					left: 32px;
					bottom: 40px;
					width: calc(100% - 64px);
					min-width: unset;
					max-width: unset;
					height: 260px;
					visibility: hidden;
					transition: all 0.3s cubic-bezier(0.12, 0, 0.39, 0) 0.4s;
				}

				@include respond-to (mobile) {
					left: 16px;
					width: calc(100% - 32px);
				}
			}

			&__menu {
				display: flex;
				flex-wrap: wrap;
				gap: clamp(20px, 4vw, 64px);
				flex-grow:1;

				@include respond-to (tablet) {
					flex-direction: column;
					margin-bottom: unset;
					gap: unset;
					width: 100%;
				}
			}

			&__img {
				font-size: 0;
				position: relative;
				height: 100%;

				img {
					height: 100%;
					object-fit: cover;
				}

				&::after {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: hsl(0, 0%, 0%);
					opacity: 0.4;
				}
			}

			&__content {
				position: absolute;
				text-align: center;
				width: 80%;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);

				@include respond-to (tablet) {
					width: 100%;
					padding: 12px;
				}

				h3 {
					font-weight: 700;
					color: hsl(0, 0%, 100%);
					line-height: 147%;
					font-size: clamp(24px, 2vw, 32px);
					margin-bottom: 4px;

					@include respond-to (tablet) {
						margin-bottom: 16px;
					}
				}
			}
		}

		.sub-menu {

			@include respond-to (tablet) {
				padding-bottom: 24px;
				transition: 0.4s ease 0.3s;

				&:not(:last-of-type) {
					padding-bottom: 48px;
				}
			}
			
			ul {
				display: flex;
				flex-flow: column wrap;
				max-height: 230px;

				@include respond-to (tablet) {
					max-height: 272px;
					max-width: max-content;
					position: relative;
					opacity: 0;
					visibility: hidden;
					margin-top: -300px;
					transition: all 0.3s cubic-bezier(0.12, 0, 0.39, 0), margin-top 0.4s cubic-bezier(0.12, 0, 0.39, 0) 0.3s;
				}

			}

			&.open {

				@include respond-to (tablet) {
					padding-bottom: 0;
					transition: 0.4s ease 0.1s;

					&:not(:last-of-type) {
						padding-bottom: 24px;
					}
				}

				ul {
					@include respond-to (tablet) {
						opacity: 1;
						visibility: visible;
						margin-top: 0;
						transition: all 0.4s cubic-bezier(0.12, 0, 0.39, 0) 0.3s, margin-top 0.4s cubic-bezier(0.12, 0, 0.39, 0);
					}
				}

				.sub-menu__title {
					@include respond-to (tablet) {
						color: var(--color-main);

						span {
							background: var(--color-main);

							&:nth-child(2) {
								transform: translateY(-50%) rotate(0);
							}
						}
					}
				}
			}

			&__title {
				font-weight: 700;
				font-size: 16px;
				line-height: 170%;
				color: hsl(0, 0%, 3%);
				margin-bottom: 8px;
				width: max-content;
				align-items: center;
				justify-content: space-between;
				transition: var(--anim4);
				pointer-events: none;

				span {
					display: block;
				}

				@include respond-to (tablet) {
					font-size: 20px;
					line-height: 27px;
					margin-bottom: 4px;
					font-weight: 400;
					width: 100%;
					position: relative;
					pointer-events: all;

					span {
						display: block;
						width: 16px;
						height: 1px;
						background: #000;
						position: absolute;
						top: 50%;
						right: 0;
						transform: translateY(-50%);
						transition: var(--anim4);

						&:nth-child(2) {
							transform: translateY(-50%) rotate(90deg);
						}
					}
				}
			}

			li {
				margin-right: clamp(15px, 3.2vw, 48px);

				@include respond-to (tablet) {
					margin-right: clamp(20px, 10vw, 75px);
					margin-bottom: 8px;
				}

				a {
					font-size: 14px;
					color: hsl(0, 0%, 3%);
					line-height: 193%;

					@include respond-to (tablet) {
						font-size: 16px;
						line-height: 27px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 920px) {
	.header__nav .dropdown {
		flex-direction: column-reverse;
		align-items: center;
	}

	.header__nav .dropdown__left {
		width: 100%;
		max-width: unset;
		margin-right: unset;
	}

	.header__nav .dropdown__img {
		max-height: 300px;
	}

	.header__nav .sub-menu li {
		margin-right: clamp(15px, 6.2vw, 48px);
	}

	.header__nav .dropdown__menu {
		gap: clamp(20px, 6.9vw, 64px);
		margin-bottom: 32px;
	}
}