.article {
	padding-top: unset;

	&__head {
		display: flex;
		align-items: stretch;
		gap: 65px;
		margin-bottom: 48px;

		@include respond-to (laptop) {
			gap: 32px;
		}

		@include respond-to (tablet) {
			flex-direction: column-reverse;
		}

		@include respond-to (mobile) {
			gap: 24px;
			margin-bottom: 16px;
		}
	}

	&__title {
		display: flex;
		flex-direction: column;
		justify-content: center;
		max-width: 445px;
		width: calc(100% - 37% - 65px);

		@include respond-to (tablet) {
			max-width: unset;
			width: 100%;
		}

		h1 {
			color: #1E1E1E;
			line-height: 1.1;

			@include respond-to (laptop) {
				font-size: 32px;
			}

			@include respond-to (mobile) {
				font-size: 24px;
				margin-bottom: 8px;
			}
		}
	}

	&__img {
		width: 37%;
		max-width: 485px;

		@include respond-to (tablet) {
			width: 100%;
			max-width: unset;
		}
	}

	&__wrap {
		display: flex;
		gap: 65px;
		align-items: flex-start;

		@include respond-to (laptop) {
			gap: 32px;
		}

		@include respond-to (tablet) {
			flex-direction: column;
		}
	}

	&__nav {
		width: 37%;
		position: sticky;
		top: 48px;

		@include respond-to (tablet) {
			width: 100%;
			position: relative;
			top: unset;
		}

		&-title {
			font-size: 24px;
			font-weight: 700;
			margin-bottom: 24px;

			@include respond-to (mobile) {
				font-size: 18px;
				margin-bottom: 8px;
			}
		}

		ul {
			max-width: 360px;

			@include respond-to (tablet) {
				max-width: unset;
			}

			li {
				margin-bottom: 16px;
				line-height: 1.5;

				a {
					display: block;
					position: relative;
					max-width: max-content;
					color: #969696;
					border-bottom: 1px solid #969696;
					margin-left: 26px;

					@include respond-to (tablet) {
						color: #222222;
						border-bottom: 1px solid var(--color-main);
					}

					@include respond-to (mobile) {
						font-size: 16px;
					}

					&::before {
						content: '';
						position: absolute;
						left: -26px;
						top: 8px;
						width: 10px;
						height: 10px;
						background: #969696;

						@include respond-to (tablet) {
							background: var(--color-main);
						}

						@include respond-to (mobile) {
							top: 50%;
							transform: translateY(-50%);
						}
					}

					&.active {
						color: #222222;
						border-bottom: 1px solid var(--color-main);

						&::before {
							background: var(--color-main);
						}
					}
				}
			}
		}
	}

	&__body {
		width: calc(100% - 37% - 65px);

		@include respond-to (tablet) {
			width: 100%;
		}

		h2 {
			font-weight: 700;

			@include respond-to (mobile) {
				margin-bottom: 8px;
				font-size: 20px;
			}
		}

		p {
			font-size: 16px;

			@include respond-to (mobile) {
				font-size: 14px;
			}
		}

		p ~ h2 {
			margin-top: 64px;

			@include respond-to (mobile) {
				margin-top: 32px;
			}
		}

		p ~ img {
			margin-top: 37px;

			@include respond-to (mobile) {
				margin-top: 5px;
			}
		}
	}
}