.blog {
	&__cards {
		--gap: 8px;
		display: flex;
		flex-wrap: wrap;
		gap: var(--gap);

		.card {
			width: calc(33.3% - (var(--gap) / 1.58));
			border: 1px solid #c4c4c4;

			@include respond-to(laptop) {
				width: calc(50% - (var(--gap) / 2));
			}

			@include respond-to(tablet) {
				width: 100%;
			}

			&__wrap {
				padding: 32px;

				@include respond-to(mobile) {
					padding: 16px 8px 8px;
				}
			}

			&__category {
				color: #616661;
				padding-bottom: 32px;
				
				@include respond-to(tablet) {
					padding-bottom: 16px;
				}

				@include respond-to(mobile) {
					padding-bottom: 8px;
					font-size: 16px;
				}
			}
			&__title {
				font-size: 24px;
				padding-bottom: 8px;
				line-height: 1.35;
			}
			&__content p{
				color: #7b7b7b;
			}

			&__img {
				overflow: hidden;
				font-size: 0;
				img {
					max-height: 200px;
					object-fit: cover;
					object-position: center;
				}
			}

			&:nth-child(5n + 1),
			&:nth-child(5n + 2) {
				width: calc(50% - (var(--gap) / 2));

				@include respond-to(tablet) {
					width: 100%;
				}

				.card {
					&__title {
						font-size: 32px;
						
						@include respond-to(laptop) {
							font-size: 24px;
						}
					}
					&__img {
						img {
							max-height: 300px;
						}
					}
				}
			}
		}
	}
	
}