.container {
	margin: 0 auto;
	padding-right: 64px;
	// padding-right: 1.25rem;
	padding-left: 64px;
	// padding-left: 1.25rem;
	max-width: 1440px;
	width: 100%;

	@include respond-to (tablet) {
		padding-left: 32px;
		padding-right: 32px;
	}

	@include respond-to (mobile) {
		padding-left: 16px;
		padding-right: 16px;
	}
}

header {
	flex: 0 0 auto;
	// position: relative;
	// z-index: 14;
	background: #fff;
}

.wrapper {
	flex: 1 0 auto;
}

footer {
	flex: 0 0 auto;
}

html {
	height: 100%;
	overflow-x: hidden;
	position: relative;	

	.opacity-bg {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: #000;
		z-index: 15;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.4s cubic-bezier(0.12, 0, 0.39, 0);

		&.js-active {
			opacity: 0.3;
			visibility: visible;

			&-header {
				opacity: 0.3;
				visibility: visible;
				z-index: 13;
			}
		}
	}
}

body {
	display: flex;
	flex: 1 0 auto;
	flex-direction: column;
	height: 100%;
}

section {
	padding-top: 64px;
	padding-bottom: 64px;

	@include respond-to (tablet) {
		padding-top: 48px;
		padding-bottom: 48px;
	}

	&.padding-sm {
		@include respond-to (tablet) {
			padding-top: 16px;
			padding-bottom: 16px;
		}
	}

	&.padding-md {
		padding-top: 32px;
		padding-bottom: 32px;

		@include respond-to (mobile) {
			padding-top: 10px;
			padding-bottom: 32px;
		}
	}
}
