.about-us .section--p0-mob {
	@include respond-to (mobile) {
		padding-top: unset;
		padding-bottom: unset;
	}
}

.connections {
	position: relative;
	overflow: hidden;
	padding: 163px 77px;

	@include respond-to (tablet) {
		padding: 80px 37px;
	}

	@include respond-to (mobile) {
		padding: unset;
		overflow: visible;
		margin-bottom: 31px;
	}

	&__img {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		img {
			height: 100%;
			object-fit: cover;
			object-position: center center;

			@include respond-to (mobile) {
				min-height: 343px;
			}
		}

		@include respond-to (mobile) {
			position: relative;
			margin-left: -16px;
			margin-right: -16px;
			width: unset;
			height: unset;
			overflow: hidden;
			margin-bottom: 46px;
		}
	}

	&__content {
		position: relative;
		z-index: 2;
		padding: 40px 65px;
		max-width: 517px;
		background: #F5F5F5;

		@include respond-to (tablet) {
			padding: 20px 35px;
			max-width: 417px;
		}

		@include respond-to (mobile) {
			padding: unset;
			max-width: unset;
			background: unset;
		}

		p {
			font-size: 16px;

			@include respond-to (mobile) {
				font-size: 14px;
			}
		}
	}
}