.reviews {
	background: #F8F8F8;

	.container {
		display: grid;
		align-items: center;
		justify-content: space-between;
		grid-template-columns: 1fr auto;
		grid-template-areas: 
			"title button"
			"slider slider";

		@include respond-to (tablet) {
			display: block;
		}
	}

	h2 {
		grid-area: title;

		@include respond-to (mobile) {
			margin-bottom: 4px;
		}
	}

	a.btn {
		grid-area: button;
		margin-bottom: 25px;

		@include respond-to (tablet) {
			margin: 0 auto;
		}
	}

	.slider-wrap {
		grid-area: slider;
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		width: 100%;
	}

	.reviews-slider {
		width: 100%;
		margin-bottom: 25px;

		@include respond-to (tablet) {
			margin-bottom: 12px;
		}
	}

	&__name {
		line-height: 27px;
		margin-bottom: 4px;
	}

	&__star {
		display: flex;
		gap: 10px;
		margin-bottom: 18px;

		svg {
			width: 18px;
			height: 18px;
		}
	}

	&__body {
		p {
			font-size: 14px;
			color: var(--color-text-light);
		}
	}

	.swiper-pagination {
		max-width: max-content;

		@include respond-to (tablet) {
			margin: 0 auto 24px;
			padding-top: 12px;
		}
	}

	.swiper-button-wrapper {
		display: flex;
		align-items: center;
		gap: 25px;
	}

	.swiper-button-prev, 
	.swiper-button-next {
		position: relative;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		transform: unset;
	}

	.swiper-button-next {
		transform: scaleX(-1);
	}

}