.section--p0 {
	padding-top: unset;
	padding-bottom: unset;
}

.section--p0 {

	@include respond-to (mobile) {
		padding-top: 24px;
		padding-bottom: 48px;
	}
}

.swiper-simple,
.swiper-simple--product {

	.swiper-slide {
		height: auto;

		img {
			height: 100%;
			object-fit: cover;
		}
	}
}