.product-desc {
	padding-top: 32px;
	padding-bottom: 32px;
	
	@include respond-to (mobile) {
		padding-top: unset;
		padding-bottom: 16px;
	}

	&__tab-header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 16px;
		flex-wrap: wrap-reverse;
		row-gap: 48px;

		@include respond-to (mobile) {
			row-gap: 24px;
		}
	}

	&__title-list {
		display: flex;
		align-items: center;
		gap: 65px;
		min-width: max-content;
		// width: 100%;
		overflow-x: scroll;
		-ms-overflow-style: none;
		scrollbar-width: none;
		overflow: -moz-scrollbars-none;
		padding: 0;
		height: auto;

		&::-webkit-scrollbar { 
			width: 0;
			padding: 0;
		}

		@include respond-to (tablet) {
			gap: 35px;
			margin-right: -32px;
			margin-left: -32px;
			width: calc(100% + 64px);
			padding-right: 32px;
			padding-left: 32px;
		}

		@include respond-to (mobile) {
			gap: 16px;
			margin-right: -16px;
			margin-left: -16px;
			width: calc(100% + 32px);
			padding-right: 16px;
			padding-left: 16px;
		}
	}
	
	&__title {
		cursor: pointer;
		padding: 4px 8px;
		min-width: max-content;
		// transition: 0.2s cubic-bezier(0.12, 0, 0.39, 0);

		&.active {
			font-weight: 700;
			color: var(--color-main);
			border-bottom: 1px solid var(--color-main);
		}

		@include respond-to (mobile) {
			font-size: 16px;
		}
	}

	&__content-item {
		display: none;
		padding: 16px 24px;

		@include respond-to (tablet) {
			padding: unset;
		}

		&.active {
			display: block;
		}

		&--p-0 {
			padding: unset;
		}
	}
	
	&__content-row {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		padding: 10px;
		margin-bottom: 8px;

		@include respond-to (mobile) {
			padding: 8px 4px;
		}


		&:nth-child(2n) {
			background: #F3F3F3;
		}
	}

	&__content-prop {
		font-size: 16px;
		color: var(--color-text);
		position: relative;
		width: 100%;
		display: flex;
		align-items: flex-end;

		&::after {
			content: '';
			border-bottom: 0.5px dashed #BDBDBD;
			flex: 1;
			min-width: 30px;
		}

		@include respond-to (mobile) {
			font-size: 14px;
		}
	}

	&__content-value {
		font-size: 16px;
		font-weight: 700;
		color: var(--color-text);
		min-width: max-content;

		@include respond-to (mobile) {
			font-size: 14px;
		}
	}

	&__description {
		max-width: 1088px;
		margin: 0 auto;

		p {
			&:not(:last-child) {
				margin-bottom: 15px;

				@include respond-to (mobile) {
					margin-bottom: 27px;
				}
			}
		}
	}

	.product-list {
		justify-content: flex-start;
		column-gap: 37px;
		row-gap: 16px;

		@include respond-to (tablet) {
			column-gap: 16px;
		}

		@include respond-to (mobile) {
			column-gap: 8px;
		}

		.product-card {
			width: calc((100% / 4) - 28px);

			@include respond-to (laptop) {
				width: calc((100% / 3) - 25px);
			}

			@include respond-to (tablet) {
				width: calc((100% / 2) - 8px);
			}

			@include respond-to (mobile) {
				width: calc((100% / 2) - 4px);
			}

			&__img {
				height: clamp(190px, 21vw, 300px);

				@include respond-to (tablet) {
					height: clamp(145px, 37vw, 300px);
					margin-bottom: 17px;
				}

				@include respond-to (mobile) {
					margin-bottom: 8px;
				}
			}

			&__price {
				font-size: 20px;
				min-width: max-content;
			}

			&__title {

				@include respond-to (tablet) {
					font-size: 16px;
				}

				@include respond-to (mobile) {
					font-size: 14px;
					margin-bottom: 2px;
				}
			}

		}

		.product-card:hover {
			unset: all;

			.product-card__title {
				color: #000;
			}
		}
	}
}