.sale {
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		align-items: stretch;
		gap: 38px;

		@include respond-to (laptop) {
			gap: 20px;
		}
	}

	&__box {
		position: relative;
		width: calc(25% - 29px);
		// flex: 1;
		max-height: 389px;

		@include respond-to (laptop) {
			width: auto;
			flex: 1;
		}

		@include respond-to (mobile) {
			flex: unset;
			width: 100%;
			height: 300px;
		}

		&--bg {
			flex: 2;

			@include respond-to (laptop) {
				flex: 2;
			}

			@include respond-to (tablet) {
				flex: unset;
				width: 100%;
			}
		}
	}

	&__desc {
		position: absolute;
		bottom: 30px;
		left: 30px;
		color: #fff;
		font-weight: 700;

		@include respond-to (laptop) {
			bottom: 32px;
			left: 16px;
		}
	}

	&__value {
		font-size: clamp(24px, 2.3vw, 32px);
		line-height: 100%;
	}

	&__title {
		font-size: clamp(16px, 1.3vw, 18px);
		line-height: 150%;
	}

	&__img {
		font-size: 0;
		height: 100%;
		img {
			height: 100%;
			object-fit: cover;
			object-position: center;
		}
	}
	
}