.thanks-page {
	.banner {
		height: calc(100vh - 143px -  282px);
		min-height: unset;

		@include respond-to (tablet) {
			height: calc(100vh - 141px -  318px);
		}

		@include respond-to (mobile) {
			height: calc(100vh - 131px -  318px);
		}

		h1 {
			font-size: 32px;
			line-height: 1.1;

			@include respond-to (mobile) {
				font-size: 24px;
				line-height: 27px;
			}
		}

		&__img {
			img {
				object-position: center bottom;

				@include respond-to (tablet) {
					object-position: 78% bottom;
				}
			}
		}

		&__content {
			max-width: 551px;

			@include respond-to (mobile) {
				max-width: unset;
				width: 100%;
			}

			p {
				margin-bottom: unset;

				
			}
		}
	}
}