html {
	font-family: "Nokora", sans-serif;
	font-size: 18px;
	cursor: default;
}

h1,
.banner-main__title {
	font-size: 64px;
	font-weight: 700;
	line-height: 94%;
	margin-bottom: 16px;
	color: #fff;

	@include respond-to (laptop) {
		font-size: 52px;
	}

	@include respond-to (tablet) {
		font-size: clamp(34px, 7vw, 64px);
		line-height: 116%;
	}

	@include respond-to (mobile) {
		font-size: 36px;
		margin-bottom: 8px;
	}
}

h2 {
	font-size: 32px;
	line-height: 1.3;
	margin-bottom: 26px;

	@include respond-to (tablet) {
		line-height: 138%;
	}

	@include respond-to (mobile) {
		font-size: 24px;
		margin-bottom: 20px;
	}
}

h3 {
	font-size: 32px;
	font-weight: 700;
	line-height: 1.1;
	margin-bottom: 24px;

	@include respond-to (mobile) {
		font-size: 24px;
	}

}

h4 {
	font-size: 18px;
	line-height: 27px;
	font-weight: 700;
	margin-bottom: 18px;
}

p {
	font-size: 18px;
	line-height: 27px;
	color: var(--color-text);

	&:not(:last-of-type) {
		margin-bottom: 27px;
	}

	@include respond-to (mobile) {
		font-size: 14px;
	}
}

b {
	font-weight: 700;
}
