.banner-main {
	position: relative;
	overflow: hidden;
	padding-top: unset;
	padding-bottom: unset;
	height: calc(100vh - 144px);
	position: relative;
	z-index: 1;

	@include respond-to (mobile) {
		height: calc(100vh - 141px);
	}

	@include respond-to (mobile) {
		height: calc(100vh - 131px);
	}

	.scroll {
		position: absolute;
		bottom: -16px;
		z-index: 10;
		left: 50%;
		font-family: "Inter";
		font-size: 16px;
		color: #fff;
		transform: rotate(-90deg);
		transform-origin: center left;
		display: flex;
		align-items: center;
		gap: 13px;

		&::before {
			content: '';
			display: block;
			width: 80px;
			height: 2px;
			background: #fff;
		}

		@include respond-to (mobile) {
			display: none;
		}
	}

	&__img {
		height: 100%;
		opacity: 0.8;

		@include respond-to (mobile) {
			opacity: 1;
		}
	}

	.banner-slider-img {
		height: 100%;
		position: relative;
		overflow: hidden;
		background: #000;
		pointer-events: none;

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 55%;
			min-width: 780px;
			height: 100%;
			background: #000;
			opacity: 0.4;
			z-index: 2;
			clip-path: polygon(0 0, 100% 0, 73% 100%, 0% 100%);
			// pointer-events: none;

			@include respond-to (tablet) {
				width: clamp(500px, 115vw, 750px);
				min-width: unset;
			}

			@include respond-to (mobile) {
				width: 140%;
				clip-path: polygon(0 0, 100% 0, 53% 100%, 0% 100%);
			}

		}

	}

	.banner-slider-img {

		img {
			height: 100%;
			object-fit: cover;
			object-position: center center;
		}
	}

	.banner-slider-wrap {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;

		.container {
			width: 100%;
		}

		.swiper-pagination {
			max-width: max-content;
		}

		.swiper-pagination-bullet {
			&::after {
				background: #fff;
			}
		}

		.swiper-pagination-bullet-active {
			&::after {
				background: #fff;
			}

			&::before {
				border: 1px solid #fff;
			}
		}
	}

	.banner-slider-content {
		max-width: 600px;
		width: 100%;
		margin-top: 149px;
		pointer-events: none;
		margin-left: unset;
		margin-right: unset;
		user-select: none;

		.swiper-slide {
			opacity: 0 !important;

			&-active {
				opacity: 1 !important;
			}
		}

		p {
			@include respond-to (tablet) {
				font-size: clamp(14px, 2.4vw, 18px);
				max-width: 450px;
			}
		}

	}

	&__title {
		max-width: 584px;
	}

	&__desc {
		max-width: 526px;

		p {
			color: #fff;
			margin-bottom: unset;
		}
	}




